import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "h3>.spinner-grow[data-v-04d3d85c]{vertical-align:middle}", "",{"version":3,"sources":["webpack://./resources/js/components/Topic/Table.vue"],"names":[],"mappings":"AAuNA,kCACA,qBACA","sourcesContent":["<template>\n    <div>\n        <topic-create></topic-create>\n        <topic-edit :topic=\"topic\" v-if=\"topic\"></topic-edit>\n\n        <h3>\n            Categories\n            <b-spinner type=\"grow\" label=\"Loading categories data\" small variant=\"secondary\" v-if=\"isLoadingData\"></b-spinner>\n        </h3>\n\n        <table-search-descriptions></table-search-descriptions>\n\n        <b-button\n            v-b-modal=\"'topic-create'\"\n            variant=\"primary\"\n            class=\"mb-4\"\n            v-b-tooltip\n            title=\"Add new category\"\n        >\n            <font-awesome-icon :icon=\"['fas', 'plus']\" />\n        </b-button>\n\n        <b-table\n            striped\n            hover\n            :fields=\"fields\"\n            :items=\"filteredItems\"\n            primary-key=\"id\"\n            thead-class=\"text-center\"\n            stacked=\"lg\"\n            :per-page=\"perPage\"\n            :current-page=\"currentPage\"\n            :sort-by.sync=\"sortBy\"\n            :sort-desc.sync=\"sortDesc\"\n            :busy=\"isBusy\"\n        >\n            <template v-slot:cell(id)=\"data\">\n                {{ data.value }}\n            </template>\n\n            <template v-slot:cell(description)=\"data\">\n                {{ data.value }}\n            </template>\n\n            <template v-slot:cell(created_at)=\"data\">\n                {{ formatDate(data.value) }}\n            </template>\n\n            <template v-slot:cell(updated_at)=\"data\">\n                {{ formatDate(data.value) }}\n            </template>\n\n            <template v-slot:cell(actions)=\"data\">\n                <b-button-group>\n                    <b-button\n                        variant=\"success\"\n                        @click=\"onOpenModal(data.item)\"\n                        v-b-tooltip\n                        title=\"Edit\"\n                    >\n                        <font-awesome-icon :icon=\"['fas', 'edit']\" />\n                    </b-button>\n                    <b-button\n                        variant=\"danger\"\n                        @click=\"showDeleteConfirmation(data.item)\"\n                        v-b-tooltip\n                        title=\"Delete\"\n                    >\n                        <font-awesome-icon :icon=\"['fas', 'trash']\" />\n                    </b-button>\n                </b-button-group>\n            </template>\n        </b-table>\n\n        <b-pagination\n            v-model=\"currentPage\"\n            :total-rows=\"totalRows\"\n            :per-page=\"perPage\"\n            align=\"center\"\n            v-if=\"totalRows > perPage\"\n        ></b-pagination>\n    </div>\n</template>\n\n<script>\nimport { mapState } from 'vuex'\nimport TopicCreate from './Create.vue'\nimport TopicEdit from './Edit.vue'\nimport TableSearchDescriptions from '../TableSearchDescriptions.vue'\nimport TableHelpers from '../../mixins/TableHelpers'\nimport TableSearchHelpers from '../../mixins/TableSearchHelpers'\nimport ToastHelpers from '../../mixins/ToastHelpers'\nimport { library } from '@fortawesome/fontawesome-svg-core'\nimport { faPlus, faEdit, faTrash } from '@fortawesome/free-solid-svg-icons'\n\nlibrary.add(faPlus, faEdit, faTrash)\n\nexport default {\n    props: ['items', 'isBusy'],\n    mixins: [TableHelpers, TableSearchHelpers, ToastHelpers],\n    components: {\n        TopicCreate,\n        TopicEdit,\n        TableSearchDescriptions\n    },\n    computed: {\n        filteredItems() {\n            // Override search method\n            if (this.form.search !== '') {\n                return this.items.filter(item => {\n                    return item.description.toLowerCase().includes(this.form.search.toLowerCase())\n                })\n            }\n\n            return this.items\n        },\n        ...mapState({\n            perPage: state => state.app.itemsPerPage\n        }),\n        fields() {\n            const fields = [\n                {\n                    key: 'id',\n                    label: 'ID',\n                    sortable: true,\n                    tdClass: ['align-middle', 'text-center']\n                },\n                {\n                    key: 'description',\n                    sortable: false,\n                    tdClass: ['align-middle', 'text-center']\n                },\n                {\n                    key: 'created_at',\n                    sortable: false,\n                    tdClass: ['align-middle', 'text-center']\n                },\n                {\n                    key: 'updated_at',\n                    sortable: false,\n                    tdClass: ['align-middle', 'text-center']\n                },\n                {\n                    key: 'actions',\n                    label: '',\n                    sortable: false,\n                    tdClass: ['align-middle', 'text-right']\n                }\n            ]\n\n            return fields\n        },\n        modalId() {\n            const type = 'edit'\n            return `topic-${type}`\n        },\n    },\n    data() {\n        return {\n            currentPage: 1,\n            sortBy: 'id',\n            sortDesc: true,\n            topic: null,\n            isLoadingData: false\n        }\n    },\n    methods: {\n        onOpenModal(topic) {\n            this.topic = topic\n            this.$nextTick(() => {\n                this.$bvModal.show(this.modalId)\n            })\n        },\n        showDeleteConfirmation(topic) {\n            this.$bvModal.msgBoxConfirm(`Are you sure you want to delete category with ID of ${topic.id}?`,\n                {\n                    title: 'Please confirm',\n                    size: 'sm',\n                    buttonSize: 'sm',\n                    okVariant: 'danger',\n                    okTitle: 'Confirm',\n                    cancelTitle: 'Cancel',\n                    footerClass: 'p-2',\n                    hideHeaderClose: false,\n                    centered: true\n                })\n                .then(value => {\n                    if (value) {\n                        this.$store.dispatch('topics/deleteTopic', topic)\n                        .catch(err => {\n                            console.error(err)\n                            this.displayHttpError(err)\n                        })\n                    }\n                })\n                .catch(err => {\n                    console.error('Delete topic confirmation dialog error', err)\n                })\n        }\n    },\n    created() {\n        this.isLoadingData = true;\n        this.$store.dispatch('topics/loadAllTopics')\n            .then(() => {\n                this.isLoadingData = false\n            })\n            .catch(error => {\n                this.isLoadingData = false\n                console.error(error)\n            })\n    }\n}\n</script>\n\n<style scoped>\nh3 > .spinner-grow {\n    vertical-align: middle;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
